.btnBlink {
  background-color: blue;
  color: aliceblue;
  border: 2px solid rgb(0, 0, 0);
  padding: 8px 13px;
  border-radius: 10px;
  font-size: 20px;
  transition: 0.4s all ease-in-out;
}
.btnBlink:hover {
  background-color: rgb(122, 138, 255);
  color: black;
}
.btnBlink:focus {
  animation: blink-btn 0.3s linear normal;
}

@keyframes blink-btn {
  0% {
    background-color: blue;
  }
  25% {
    background-color: white;
  }
  100% {
    background-color: blue;
  }
}

.suggestion-group {
  position: relative;
  max-height: 200px;
  overflow-y: auto;
  z-index: 10;
  min-width: 240px;
  width: fit-content;
  max-width: 300px;
 
  
}

.suggestion-text {
  position: relative;
  text-decoration: none;
  list-style: none;
  width: 400px;
  max-width: 300px;
}

.suggestion {
  border: 1px solid black;
  transition: 0.2s all ease-in-out;
  cursor: pointer;
}

.suggestion:hover {
  background: rgb(123, 123, 123);
  color: white;
}

.animate-suggestion{
    animation: blink-suggestion 0.6s linear normal;
}

@keyframes blink-suggestion {
  0% {
    background-color: black;
    color: white;
  }
  50% {
    background-color: white;
    color: black;
  }
  100% {
    background-color: black;
    color: white;

  }
}


.image-block{
    width: fit-content;
    transition: 0.2s all ease-in-out;
    background:white;
    border: 2px solid silver;

}

.image-block:hover{
    background: rgb(123, 123, 123);
  color: white; 
}
.image-block:focus{
    animation: image-selection 0.6s linear normal;

}
.image-selected{
    animation: image-selection 0.6s linear normal;

}

@keyframes image-selection {
    0% {
      background-color: black;
      color: white;
    }
    50% {
      background-color: white;
      color: black;
    }
    100% {
      background-color: black;
      color: white;
  
    }
  }